@import '../../../util/variables.scss';

@mixin contactBtn {
	background-color: $rare;
	border: 1.5px solid $primary;
	border-radius: 3px;
	height: 2.5rem;
	width: 8rem;
	font-size: 1rem;
	outline: none;
	cursor: pointer;
}

.header {
	top: 0;
	display: flex;
	justify-content: space-between;
	align-items: center;
	justify-content: flex-end;
	position: fixed;
	background-color: $rare;
	width: 100%;
	height: 4rem;
	.link {
		color: $burnt;
		text-decoration: none;
		.logo {
			h1 {
				position: absolute;
				top: 0.1rem;
				left: 0;
				font-family: 'Georgia';
				font-weight: normal;
				font-size: 38px;
				z-index: 1;
				margin: 1.5rem 0 0 5rem;
			}
		}
	}
	.rectangle {
		position: absolute;
		top: 1.2rem;
		left: 7.8rem;
		width: 134px;
		height: 64px;
		background-color: $primary;
		z-index: 0;
	}
	.nav {
		display: flex;
		justify-content: space-evenly;
		align-items: center;
		width: 40%;
		h3 {
			font-weight: normal;
			font-family: 'Open Sans';
			font-size: 1rem;
			cursor: pointer;
			margin: 5px 0;
			padding: 10px;
			display: block;
			position: relative;
			z-index: 1;
			transition: all .5s;
			&:after {
				content: '';
				display: block;
				width: 0;
				height: 2px;
				background: $secondAccent;
				transition: width .5s;
			}
			&:hover:after {
				width: 100%;
			}
		}
		.contact {
			@include contactBtn();
			transition: all 0.3s ease-in-out;
			&:hover {
				background-color: $primary;
			}
		}
	}
}

@media screen and (min-width: $breakpoint-mobileSM) and (max-width: $breakpoint-mobile) {
	.header {
		height: 2rem;
		background-color: none;
		.link {
			.logo {
				display: none;
			}
		}
		.rectangle {
			display: none;
		}
		.nav {
			display: none;
		}
		#menuToggle {
			#menu {
				width: 200px;
			}
		}
	}
}

@media screen and (min-width: $breakpoint-mobile) and (max-width: $breakpoint-phablet) {
	.header {
		.link {
			.logo {
				display: none;
			}
		}
		.rectangle {
			display: none;
		}
		.nav {
			display: none;
		}
	}
}

@media screen and (min-width: $breakpoint-tablet) and (max-width: $breakpoint-desktop) {
	.header {
		.nav {
			width: 70%;
		}
		#menuToggle {
			display: none;
		}
	}
}

@media screen and (min-width: $breakpoint-desktop) {
	.header {
		#menuToggle {
			display: none;
		}
	}
}
