@import '../../../util/variables.scss';

@mixin contactBtn {
	background-color: $rare;
	border: 1.5px solid $primary;
	border-radius: 3px;
	height: 2.5rem;
	width: 9rem;
	font-size: 1.2rem;
	outline: none;
	cursor: pointer;
}

@keyframes slideInFromLeft {
	0% {
		transform: translateX(-200%);
	}
	100% {
		transform: translateX(0);
	}
}

.header {
	top: 0;
	display: flex;
	justify-content: space-between;
	align-items: center;
	justify-content: flex-end;
	position: fixed;
	background-color: $rare;
	width: 100%;
	height: 5rem;
	.link {
		color: $burnt;
		text-decoration: none;
		.logo {
			h1 {
				position: absolute;
				top: 0.1rem;
				left: 0;
				font-family: 'Georgia';
				font-weight: normal;
				font-size: 40px;
				z-index: 1;
				margin: 1.5rem 0 0 5rem;
			}
		}
	}
	.rectangle {
		position: absolute;
		top: 1.2rem;
		left: 7.8rem;
		width: 134px;
		height: 64px;
		background-color: $primary;
		z-index: 0;
	}
	h2 {
		font-family: 'Open Sans';
		font-weight: normal;
		font-size: 20px;
		color: $mediumWell;
		margin-right: 3rem;
		cursor: pointer;
		display: inline-block;
		position: relative;
		transition: 0.5s;
	}
	h2:after {
		content: '\2190';
		position: absolute;
		opacity: 0;
		top: 0;
		left: -20px;
		transition: 0.3s;
	}

	h2:hover {
		color: $burnt;
		padding-left: 20px;
	}
	h2:hover:after {
		opacity: 1;
		right: 0;
	}
}

.side-nav {
	animation: 1.3s ease-out 0s 1 slideInFromLeft;
	position: fixed;
	top: 20%;
	left: 5%;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	border-left: solid $secondAccent;
	.link {
		color: $burnt;
		text-decoration: none;
	}
	h3 {
		font-family: 'Open Sans';
		font-weight: normal;
		font-size: 1rem;
		margin: 9px 0;
		padding: 5px;
		cursor: pointer;
		display: block;
		position: relative;
		z-index: 1;
		transition: all .5s;
		// &:after {
		//     display: block;
		//     position: absolute;
		//     top: 0;
		//     left: 0;
		//     bottom: 0;
		//     right: 0;
		//     margin: auto;
		//     width: 100%;
		//     height: 1px;
		//     content: '.';
		//     color: transparent;
		//     background: $secondAccent;
		//     visibility: none;
		//     opacity: 0;
		//     z-index: -1;
		//     transition: all .6s;
		// }
		// &:hover:after{
		//     opacity: 1;
		//     visibility: visible;
		//     height: 100%;
		// }
		&:after {
			content: '';
			display: block;
			width: 0;
			height: 2px;
			background: $secondAccent;
			transition: width .5s;
		}
		&:hover:after {
			width: 100%;
		}
	}

	.contact {
		@include contactBtn();

		&:hover {
			background-color: $primary;
		}
	}
}

@media screen and (min-width: $breakpoint-mobileSM) and (max-width: $breakpoint-phablet) {
	.side-nav {
		display: none;
	}
}
